import React, { useState } from 'react';
import './Analytics.css';
import { TrendingUp, BarChart3, PieChart, Activity, Target } from 'lucide-react';
import { 
  AreaChart,
  Area,
  XAxis, 
  YAxis, 
  CartesianGrid, 
  Tooltip, 
  ResponsiveContainer, 
  BarChart, 
  Bar, 
  LineChart,
  Line,
  ComposedChart,
  Legend,
  Cell
} from 'recharts';
import CustomTooltip from './CustomTooltip';

type DashboardProps = {
  sidebarCollapsed?: boolean;
};

//    
const dynamicsData = [
  { month: ' 1', creatives: 45000 },
  { month: ' 2', creatives: 50000 },
  { month: ' 3', creatives: 48000 },
  { month: ' 4', creatives: 58000 },
  { month: ' 5', creatives: 62000 },
  { month: ' 6', creatives: 68000 },
  { month: ' 7', creatives: 71000 },
  { month: ' 8', creatives: 69000 },
  { month: ' 9', creatives: 73000 },
  { month: ' 10', creatives: 75000 },
  { month: ' 11', creatives: 76000 },
  { month: ' 12', creatives: 78000 }
];

//    
const topCategoriesData = [
  { category: '', creatives: 185000, color: '#8b5cf6' },
  { category: '', creatives: 160000, color: '#22c55e' },
  { category: '  ', creatives: 95000, color: '#f59e0b' },
  { category: '', creatives: 115000, color: '#06b6d4' },
  { category: '  ', creatives: 88000, color: '#fb7185' }
];

//    
const topSubcategoriesData = [
  { subcategory: '', creatives: 95000, color: '#f59e0b' },
  { subcategory: ' ', creatives: 88000, color: '#06b6d4' },
  { subcategory: ' ', creatives: 75000, color: '#fb7185' },
  { subcategory: ' ', creatives: 65000, color: '#ef4444' },
  { subcategory: '', creatives: 58000, color: '#fbbf24' }
];

//      
const forecastData = [
  { date: '2025-01-01', actual: 220, forecast: null },
  { date: '2025-01-07', actual: 240, forecast: null },
  { date: '2025-01-14', actual: 260, forecast: null },
  { date: '2025-01-21', actual: 280, forecast: null },
  { date: '2025-01-28', actual: 300, forecast: 300 },
  { date: '2025-02-04', actual: null, forecast: 320 },
  { date: '2025-02-11', actual: null, forecast: 335 },
  { date: '2025-02-18', actual: null, forecast: 345 },
  { date: '2025-02-25', actual: null, forecast: 360 }
];

//    
const seasonalData = [
  { month: '', index: 0.8 },
  { month: '', index: 0.9 },
  { month: '', index: 1.1 },
  { month: '', index: 1.2 },
  { month: '', index: 1.0 },
  { month: '', index: 0.9 },
  { month: '', index: 0.8 },
  { month: '', index: 1.3 },
  { month: '', index: 1.4 },
  { month: '', index: 1.4 },
  { month: '', index: 1.5 },
  { month: '', index: 1.5 }
];

const Dashboard: React.FC<DashboardProps> = ({ sidebarCollapsed = false }) => {
  const [filters, setFilters] = useState({
    category: ' ',
    subcategory: ' '
  });

  const handleFilterChange = (filterName: string, value: string) => {
    setFilters(prev => ({
      ...prev,
      [filterName]: value
    }));
  };

  const CustomTooltip = ({ active, payload, label }: any) => {
    if (active && payload && payload.length) {
      return (
        <div className="dashboard-tooltip">
          <div className="tooltip-header">
            <span className="tooltip-label">{label}</span>
          </div>
          <div className="tooltip-content">
            {payload.map((entry: any, index: number) => (
              <div key={index} className="tooltip-item">
                <div 
                  className="tooltip-dot" 
                  style={{ backgroundColor: entry.color }}
                />
                <span className="tooltip-name">{entry.name}</span>
                <div className="tooltip-value" style={{ color: entry.color }}>
                  {entry.value?.toLocaleString()}
                </div>
              </div>
            ))}
          </div>
        </div>
      );
    }
    return null;
  };

  const CustomBarTooltip = ({ active, payload, label }: any) => {
    if (active && payload && payload.length) {
      const data = payload[0].payload;
      return (
        <div className="dashboard-tooltip">
          <div className="tooltip-header">
            <span className="tooltip-label">{data.category || data.subcategory}</span>
          </div>
          <div className="tooltip-content">
            <div className="tooltip-item">
              <div 
                className="tooltip-dot" 
                style={{ backgroundColor: data.color }}
              />
              <span className="tooltip-name"></span>
              <div className="tooltip-value" style={{ color: data.color }}>
                {data.creatives?.toLocaleString()}
              </div>
            </div>
          </div>
        </div>
      );
    }
    return null;
  };

  const CustomForecastTooltip = ({ active, payload, label }: any) => {
    if (active && payload && payload.length) {
      return (
        <div className="dashboard-tooltip">
          <div className="tooltip-header">
            <span className="tooltip-label">{label}</span>
          </div>
          <div className="tooltip-content">
            {payload.map((entry: any, index: number) => (
              <div key={index} className="tooltip-item">
                <div 
                  className="tooltip-dot" 
                  style={{ backgroundColor: entry.color }}
                />
                <span className="tooltip-name">{entry.name}</span>
                <div className="tooltip-value" style={{ color: entry.color }}>
                  {entry.value}
                </div>
              </div>
            ))}
          </div>
        </div>
      );
    }
    return null;
  };

  return (
    <div className="dashboard-page">

      {/*  */}
      <div className="dashboard-filters">
        <div className="filter-group">
          <label className="filter-label"></label>
          <select 
            className="filter-select"
            value={filters.category}
            onChange={(e) => handleFilterChange('category', e.target.value)}
          >
            <option> </option>
            <option></option>
            <option></option>
            <option>  </option>
            <option></option>
            <option>  </option>
          </select>
        </div>
        
        <div className="filter-group">
          <label className="filter-label"></label>
          <select 
            className="filter-select"
            value={filters.subcategory}
            onChange={(e) => handleFilterChange('subcategory', e.target.value)}
          >
            <option> </option>
            <option></option>
            <option></option>
            <option></option>
            <option> </option>
            <option> </option>
          </select>
        </div>
      </div>

      {/*   */}
      <div className="dashboard-grid">
        {/*   */}
        <div className="dashboard-card">
          <h3><TrendingUp size={20} /> </h3>
          <div className="chart-container">
            <ResponsiveContainer width="100%" height="100%">
              <AreaChart data={dynamicsData}>
                <CartesianGrid strokeDasharray="3 3" stroke="#374151" opacity={0.1} />
                <XAxis 
                  dataKey="month" 
                  tick={{ fontSize: 12, fill: '#9ca3af' }}
                  axisLine={{ stroke: '#374151', opacity: 0.2 }}
                />
                <YAxis 
                  tick={{ fontSize: 12, fill: '#9ca3af' }}
                  axisLine={{ stroke: '#374151', opacity: 0.2 }}
                />
                <Tooltip content={<CustomTooltip />} />
                <Area
                  type="monotone"
                  dataKey="creatives"
                  stroke="#8b5cf6"
                  fill="#8b5cf6"
                  fillOpacity={0.3}
                  strokeWidth={3}
                  name=" "
                />
              </AreaChart>
            </ResponsiveContainer>
          </div>
        </div>

        {/*   */}
        <div className="dashboard-card">
          <h3><BarChart3 size={20} /> </h3>
          <div className="chart-container">
            <ResponsiveContainer width="100%" height={300}>
              <BarChart data={topCategoriesData} layout="vertical" margin={{ left: 20, right: 20 }}>
                <CartesianGrid strokeDasharray="3 3" stroke="#374151" opacity={0.1} />
                <XAxis 
                  type="number"
                  domain={[0, 'dataMax']}
                  tick={{ fontSize: 12, fill: '#9ca3af' }}
                  axisLine={{ stroke: '#374151', opacity: 0.2 }}
                />
                <YAxis 
                  type="category"
                  dataKey="category"
                  tick={{ fontSize: 12, fill: '#9ca3af' }}
                  axisLine={{ stroke: '#374151', opacity: 0.2 }}
                  width={120}
                />
                <Tooltip content={<CustomTooltip />} />
                <Bar 
                  dataKey="creatives" 
                  radius={[0, 4, 4, 0]}
                  name=""
                >
                  {topCategoriesData.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={entry.color} />
                  ))}
                </Bar>
              </BarChart>
            </ResponsiveContainer>
          </div>
        </div>
      </div>

      {/*   ( ) */}
      <div className="dashboard-card" style={{ marginBottom: '24px' }}>
        <h3><PieChart size={20} /> </h3>
        <div className="chart-container">
          <ResponsiveContainer width="100%" height={300}>
            <BarChart data={topSubcategoriesData}>
              <CartesianGrid strokeDasharray="3 3" stroke="#374151" opacity={0.1} />
              <XAxis 
                dataKey="subcategory" 
                tick={{ fontSize: 12, fill: '#9ca3af' }}
                axisLine={{ stroke: '#374151', opacity: 0.2 }}
                angle={-45}
                textAnchor="end"
                height={80}
              />
              <YAxis 
                tick={{ fontSize: 12, fill: '#9ca3af' }}
                axisLine={{ stroke: '#374151', opacity: 0.2 }}
              />
              <Tooltip content={<CustomTooltip />}  />
              <Bar 
                dataKey="creatives" 
                radius={[4, 4, 0, 0]}
                name=""
              >
                {topSubcategoriesData.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={entry.color} />
                ))}
              </Bar>
            </BarChart>
          </ResponsiveContainer>
        </div>
      </div>

      {/*   */}
      <div className="dashboard-card dashboard-full-width">
        <h3><Activity size={20} /> </h3>
        <div className="trends-container">
          {/*     */}
          <div className="trend-card">
            <h4><Target size={16} />   </h4>
            <div className="chart-container">
              <ResponsiveContainer width="100%" height="100%">
                <ComposedChart data={forecastData}>
                  <CartesianGrid strokeDasharray="3 3" stroke="#374151" opacity={0.1} />
                  <XAxis 
                    dataKey="date" 
                    tick={{ fontSize: 12, fill: '#9ca3af' }}
                    axisLine={{ stroke: '#374151', opacity: 0.2 }}
                    tickFormatter={(value) => new Date(value).toLocaleDateString('ru-RU', { day: '2-digit', month: '2-digit' })}
                  />
                  <YAxis 
                    tick={{ fontSize: 12, fill: '#9ca3af' }}
                    axisLine={{ stroke: '#374151', opacity: 0.2 }}
                  />
                  <Tooltip content={<CustomTooltip />} />
                  <Legend />
                  <Area
                    type="monotone"
                    dataKey="actual"
                    fill="#8b5cf6"
                    fillOpacity={0.3}
                    stroke="#8b5cf6"
                    strokeWidth={2}
                    name=" "
                  />
                  <Line
                    type="monotone"
                    dataKey="forecast"
                    stroke="#22c55e"
                    strokeWidth={2}
                    strokeDasharray="4 4"
                    dot={false}
                    name=""
                  />
                </ComposedChart>
              </ResponsiveContainer>
            </div>
            <p className="trend-description">
                  8-12%     
              .       -.
            </p>
          </div>

          {/*   */}
          <div className="trend-card">
            <h4><Activity size={16} /> </h4>
            <div className="chart-container">
              <ResponsiveContainer width="100%" height="100%">
                <AreaChart data={seasonalData} className="seasonal-chart">
                  <CartesianGrid strokeDasharray="3 3" stroke="#374151" opacity={0.1} />
                  <XAxis 
                    dataKey="month" 
                    tick={{ fontSize: 12, fill: '#9ca3af' }}
                    axisLine={{ stroke: '#374151', opacity: 0.2 }}
                  />
                  <YAxis 
                    tick={{ fontSize: 12, fill: '#9ca3af' }}
                    axisLine={{ stroke: '#374151', opacity: 0.2 }}
                  />
                  <Tooltip content={<CustomTooltip />} />
                  <Area
                    type="monotone"
                    dataKey="index"
                    stroke="#f59e0b"
                    fill="#f59e0b"
                    fillOpacity={0.3}
                    strokeWidth={3}
                    name=" "
                  />
                </AreaChart>
              </ResponsiveContainer>
            </div>
            <p className="trend-description">
               : - (+26%), - (+30%),  (+40%). 
               : - (-15%).
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;