import React, { useState } from 'react';
import './Monitoring.css';
import { Tv, Radio, Play, Image, MapPin } from 'lucide-react';
import CreativeModal from './CreativeModal';

interface MonitoringRow {
  date: string;
  brand: string;
  category: string;
  subcategory: string;
  media: string;
  ots: string;
  status: string;
}

const mockData: MonitoringRow[] = [
  {
    date: '15.07.2025',
    brand: '',
    category: '',
    subcategory: '',
    media: '',
    ots: '2 500 000',
    status: '',
  },
  {
    date: '16.07.2025',
    brand: 'Toyota',
    category: '',
    subcategory: ' ',
    media: ' ',
    ots: '1 800 000',
    status: '',
  },
  {
    date: '17.07.2025',
    brand: 'Coca-Cola',
    category: '  ',
    subcategory: ' ',
    media: '',
    ots: '1 200 000',
    status: '',
  },
  {
    date: '18.07.2025',
    brand: '',
    category: '',
    subcategory: ' ',
    media: ' ',
    ots: '850 000',
    status: '',
  },
  {
    date: '19.07.2025',
    brand: 'Pepsi',
    category: '  ',
    subcategory: ' ',
    media: ' ',
    ots: '920 000',
    status: '',
  },
];

const statsData = [
  {
    icon: <Tv size={24} />,
    title: '',
    value: '198,731',
    subtitle: ' ',
    type: 'tv'
  },
  {
    icon: <Radio size={24} />,
    title: '',
    value: '85,085',
    subtitle: ' ',
    type: 'radio'
  },
  {
    icon: <Play size={24} />,
    title: ' ',
    value: '141,809',
    subtitle: '    ',
    type: 'online'
  },
  {
    icon: <Image size={24} />,
    title: ' ',
    value: '113,447',
    subtitle: ' ',
    type: 'banner'
  },
  {
    icon: <MapPin size={24} />,
    title: ' ',
    value: '28,162',
    subtitle: ' ',
    type: 'outdoor'
  }
];

const getMediaBadgeClass = (media: string): string => {
  switch (media) {
    case '':
      return 'media-badge tv';
    case '':
      return 'media-badge radio';
    case ' ':
      return 'media-badge online';
    case ' ':
      return 'media-badge banner';
    case ' ':
      return 'media-badge outdoor';
    default:
      return 'media-badge';
  }
};

const Monitoring: React.FC<{ sidebarCollapsed?: boolean }> = ({ sidebarCollapsed = false }) => {
  const [filters, setFilters] = useState({
    category: ' ',
    subcategory: ' ',
    mediaType: ' ',
    period: ''
  });

  const [modalData, setModalData] = useState<MonitoringRow | null>(null);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const handleFilterChange = (filterName: string, value: string) => {
    setFilters(prev => ({
      ...prev,
      [filterName]: value
    }));
  };

  const handleDetailsClick = (rowData: MonitoringRow) => {
    setModalData(rowData);
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
    setModalData(null);
  };

  return (
    <div className={`monitoring-page ${sidebarCollapsed ? 'sidebar-collapsed' : ''}`}>
      {/* <h1> </h1> */}
      
      {/*  */}
      <div className="monitoring-filters">
        <div className="filter-group">
          <label className="filter-label"></label>
          <select 
            className="filter-select"
            value={filters.category}
            onChange={(e) => handleFilterChange('category', e.target.value)}
          >
            <option> </option>
            <option></option>
            <option></option>
            <option>  </option>
            <option></option>
          </select>
        </div>
        
        <div className="filter-group">
          <label className="filter-label"></label>
          <select 
            className="filter-select"
            value={filters.subcategory}
            onChange={(e) => handleFilterChange('subcategory', e.target.value)}
          >
            <option> </option>
            <option></option>
            <option> </option>
            <option> </option>
            <option> </option>
          </select>
        </div>
        
        <div className="filter-group">
          <label className="filter-label"> </label>
          <select 
            className="filter-select"
            value={filters.mediaType}
            onChange={(e) => handleFilterChange('mediaType', e.target.value)}
          >
            <option> </option>
            <option></option>
            <option></option>
            <option> </option>
            <option> </option>
            <option> </option>
          </select>
        </div>
        
        <div className="filter-group">
          <label className="filter-label"></label>
          <select 
            className="filter-select"
            value={filters.period}
            onChange={(e) => handleFilterChange('period', e.target.value)}
          >
            <option></option>
            <option></option>
            <option></option>
            <option></option>
          </select>
        </div>
      </div>

      {/*   */}
      <div className="monitoring-stats">
        {statsData.map((stat, index) => (
          <div key={index} className="stat-card">
            <div className="stat-card-header">
              <div className={`stat-icon ${stat.type}`}>
                {stat.icon}
              </div>
              <div>
                <h3 className="stat-title">{stat.title}</h3>
                <p className="stat-value">{stat.value}</p>
              </div>
            </div>
            <p className="stat-subtitle">{stat.subtitle}</p>
          </div>
        ))}
      </div>

      {/*   */}
      <div className="monitoring-table-wrapper">
        <table className="monitoring-table">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th>OTS</th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {mockData.map((row, index) => (
              <tr key={index}>
                <td>{row.date}</td>
                <td className="brand-name">{row.brand}</td>
                <td>{row.category}</td>
                <td>{row.subcategory}</td>
                <td>
                  <span className={getMediaBadgeClass(row.media)}>
                    {row.media}
                  </span>
                </td>
                <td className="ots-value">{row.ots}</td>
                <td>
                  <span className="status-badge">{row.status}</span>
                </td>
                <td>
                  <button 
                    className="action-button"
                    onClick={() => handleDetailsClick(row)}
                  >
                    
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/*   */}
      {modalData && (
        <CreativeModal 
          isOpen={isModalOpen}
          onClose={handleCloseModal}
          data={modalData}
        />
      )}
    </div>
  );
};

export default Monitoring;